	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Progetto \"Il Tempio della Grande Madre\"</strong><br>\n(di Laura Ghianda)</p>\n<p>Frequentai il Glastonbury Goddess Temple (Tempio della Dea di Glastonbury) per la prima volta nel 2006, quando iniziai il mio primo anno di formazione alla scuola del tempio.\nQuello spazio mi conquistò.<br>\nNon ha nulla a che vedere con altri luoghi sacri \"al chiuso\" perché fondati sull'idea che il \"sacro debba essere protetto dal profano\", che ho potuto incontrare nella mia vita. Qui siamo dinanzi a qualcosa di davvero differente.<br>\nVibra di qualcosa di antico e nuovo assieme, una potenza dimenticata, spaventosa ma allo stesso tempo incredibilmente accogliente. È l'energia di quella Grande Madre venerata pressoché ovunque in tempi arcaici sulla Terra, ma quasi ormai dimenticata dalle culture patriarcali di cui è parte anche la nostra cultura occidentale.<br>\nAd aprile del 2007 raggiunsi sulle stampelle BridÈs Mound, un'antica collinetta alla periferia di Glastonbury, antico luogo di culto dedicato alla Dea Brigit – e quindi a Santa Brigida. Ancora oggi i pellegrini sono soliti recarvisi muniti di un nastro o un pezzo di tessuto (naturale!) da legare ai rami di un albero chiedendo a Brigit (o Santa Brigida) guarigione, guida o affidandole i propri desideri.<br>\nSi lega il tessuto, si lascia un'offerta (libagione o erbe o altro) e si pronuncia la frase in modo affermativo, coniugata al presente. Viste le condizioni non felici della mia gamba e il rischio di restare zoppa per il resto della mia vita, la prima offerta disse \"guarisco e torno a camminare come prima\".<br>\nMa la seconda fu \"apro un Tempio della Dea in Italia\".<br>\nUna cosa detta d'istinto, di cuore, con un sacco di paura e di timore. Ancora non ero consapevole del nostro ruolo, come esseri umani, di co-creatrici/creatori, né del nostro potere di agire nel mondo. (Leggi articolo sul potere di noi tutte/i <a href=\"http://lauraghianda.blogspot.it/2014/03/la-freccia-di-diana-riflessione-sul.html\" target=\"_blank_\">qui</a>) Non sapevo nemmeno se sarebbe veramente accaduto!</p>\n<p>Da allora sono passati 10 anni e moltissime cose sono cambiate. Molto si è mosso. Altre persone in Italia hanno fatto esperienza di quel luogo-tempio, altre hanno concluso o cominciato la formazione alla sua scuola. Il sogno si è fatto collettivo. Abbiamo iniziato a collaborare, incontrarci, condividere idee, conoscenze, esperienze, mettendo in comune le nostre risorse.<br>\nCiascuna di noi, lavorando nella sua realtà locale.</p>\n<p>Il sogno più ambizioso della nostra associazione è pervenire, nel tempo, alla possibilità di aprire una sede stabile da adibire a moderno \"Tempio della Grande Madre\", sul modello del <a href=\"http://www.goddesstemple.co.uk\" target=\"_blank_\">Glastonbury Goddess Temple</a> e del <a href=\"http://www.goddesstempleashland.com/\" target=\"_blank_\">Goddess Temple di Ashland</a> (USA), dove custodire la <a href=\"fiamma-delle-dolomiti\">Fiamma della Grande Madre delle Dolomiti</a>, riaccesa cerimonialmente il 20 settembre 2015.\nA questo scopo raccoglieremo il denaro derivante dai corsi, per-corsi e nostre proposte, dalle donazioni e dal crowdfunding (questa è la nostra <a href=\"https://buonacausa.org/cause/tempiodellagrandemadre\" target=\"_blank_\">pagina</a>). Tutto quello che non verrà nel corso dell'anno re-investito in altri progetti secondo i fini dell\"organizzazione, sarà messo da parte in un fondo per realizzare il nostro sogno.</p>\n<p>Se vuoi contribuire a questo sogno e ai nostri progetti, anche con piccole donazioni, ti ringraziamo dal profondo del nostro cuore!</p>\n<form action=\"https://www.paypal.com/cgi-bin/webscr\" method=\"post\" target=\"_blank_\">\n<input type=\"hidden\" name=\"cmd\" value=\"_s-xclick\">\n<input type=\"hidden\" name=\"hosted_button_id\" value=\"XX9ES4XG3K54C\">\n<input type=\"image\" src=\"https://www.paypalobjects.com/it_IT/IT/i/btn/btn_donateCC_LG.gif\" border=\"0\" name=\"submit\"_>\n<img alt=\"\" border=\"0\" src=\"https://www.paypalobjects.com/it_IT/i/scr/pixel.gif\" width=\"1\" height=\"1\">\n</form>\n<p>Nel frattempo ci muoveremo per le provincie di Trento, Bolzano, Belluno e ovunque ci chiamino con il progetto \"Tempio Temporaneo e Itinerante\", allestendo come Tempio gli spazi che prenderemo brevemente in affitto per l'occasione (o meglio ancora, con un po' di fortuna, il cui uso ci sarà donato per il tempo necessario alla nostra iniziativa). Connetteremo energeticamente il nostro tempio a quello di Glastonbury, che ci sostiene in questo progetto, e lo metteremo a disposizione delle persone che vogliono farne esperienza. Il tempo sarà suddiviso tra momenti strutturati con proposte di laboratori, workshop, attività e momenti di apertura dove stare raccolti, meditare, riposare, scrivere, pregare, sognare, divinare, ecc. in uno spazio che rappresenta il caldo grembo della Grande Madre.</p>\n<p>E se invece hai una vecchia sala, grande cantina, grande stanza, ex magazzino o qualsiasi locale inutilizzato dotato di minimi standard igienici (finestra e bagno), ti piacerebbe dargli un senso e \"prestarcelo\" (comodato d'uso o affitto simbolico): lo possiamo trasformare in \"Tempio della Grande Madre\", sede della nostra associazione, possibilmente non troppo lontano dalla città/paesi limitrofi. Anche a tempo determinato!\nCe ne prenderemo cura, lo addobberemo, lo terremo pulito, profumato e lo  renderemo \"spazio sacro\".<br>\n<a href=\"/contatti\"><strong>Contattaci!</strong></a></p>\n<br/>\n<p>Per saperne di più:<br>\n<a href=\"http://lauraghianda.blogspot.it/2014/09/il-sogno-e-la-visione-del-trento.html\" target=\"_blank_\">\"Il sogno e la visione del Tempio della Grande Madre di Trento\"</a><br>\n<a href=\"http://lauraghianda.blogspot.it/search?q=tempio+coperto\" target=\"_blank_\">\"Tempio coperto, tempio scoperto. Come è fuori, così è dentro. Tutto è sacro.\"</a></p>","frontmatter":{"path":"/progetto","title":"Progetto"}}},"pathContext":{}}

